.quiz{
    margin: auto;
    padding-bottom: 100px;
    width: 100%;

    @media (max-width: 600px) {
        padding-top: 50px;
        padding-bottom: 30px;
        max-width: unset;
    }

    .header{
        margin: auto;
        width: 95%;

        @media (max-width: 480px) {
            margin-left: 0%;
            width: 100%;
        }
        .sub-title{
            color: var(--secondary-highlight);
            font-size: 1rem;
            font-family: 'ThicccBoi';
            font-weight: 600;
            letter-spacing: 6px;
            margin: 0px 0px 12px 0px;

            @media (max-width: 600px) {
                font-size: 0.6rem;
            }
        }

        .title-box{
            width: 100%;
            overflow: hidden;
            margin-bottom: 18px;
            .container{
                width: 200%;
                h3{
                    display: inline-block;
                    font-family: 'Space Grotesk';
                    font-size: 4rem;
                    letter-spacing: -0.15rem;
                    color: var(--primary-highlight);
                    margin: 0px 18px 0px 0px;
                    vertical-align: middle;
                    font-weight: 400;

                    @media (max-width: 600px) {
                        font-size: 2rem;
                    }
                }
                .title-line{
                    display: inline-block;
                    height: 1px;
                    width: 50%;
                    background: var(--text-color);
                    vertical-align: middle;
                }
            }
        }
    }
    .questions{
        width: 95%;
        margin: auto;
        font-family: 'DM Sans';
        font-size: 1.2rem;

        @media (max-width: 600px){
            font-size: 0.9rem;
        }

        .question-container{
            margin-bottom: 60px;

            .question{
                line-height: 24px;
                margin-bottom: 24px;
            }
            .options{
                .option{
                    border: 1px solid #2C2C2C;
                    box-sizing: border-box;
                    border-radius: 2px;
                    padding: 12px 12px 12px 30px;
                    margin: 8px 0px;
                    position: relative;
                    transition-duration: 0.25s;

                    .number{
                        position: absolute;
                        background: white;
                        width: 20px;
                        height: 20px;
                        border-radius: 10px;
                        top: 50%;
                        left: 0px;
                        transform: translate(-50%, -50%);
                        text-align: center;
                        color: #FF6534;
                        line-height: 20px;
                        font-weight: bold;
                        font-size: 1rem;

                        @media (max-width: 600px){
                            font-size: 0.7rem;
                        }

                        .tick{
                            display: none;
                        }

                        .cross{
                            display: none;
                        }
                    }

                    &.correct{
                        border-color: #31ac13;

                        .number{
                            line-height: 28px;
                            span{
                                display: none;
                            }
                            .tick{
                                display: inline-block;
                            }
                        }
                    }

                    &.wrong{
                        border-color: #ff5454;

                        .number{
                            line-height: 27px;
                            span{
                                display: none;
                            }
                            .cross{
                                display: inline-block;
                            }
                        }
                    }
                }
            }
        }
    }
}