.post{
    margin: auto;
    padding-bottom: 50px;
    width: 100%;

    @media (max-width: 600px) {
        padding-top: 50px;
        padding-bottom: 30px;
        max-width: unset;
    }

    .header{
        margin: auto;
        width: 95%;

        @media (max-width: 480px) {
            margin-left: 0%;
            width: 100%;
        }
        .sub-title{
            color: var(--secondary-highlight);
            font-size: 1rem;
            font-family: 'ThicccBoi';
            font-weight: 600;
            letter-spacing: 6px;
            margin: 0px 0px 12px 0px;

            @media (max-width: 600px) {
                font-size: 0.6rem;
            }
        }

        .title-box{
            width: 100%;
            overflow: hidden;
            margin-bottom: 18px;
            .container{
                width: 200%;
                h3{
                    display: inline-block;
                    font-family: 'Space Grotesk';
                    font-size: 4rem;
                    letter-spacing: -0.15rem;
                    color: var(--primary-highlight);
                    margin: 0px 18px 0px 0px;
                    vertical-align: middle;
                    font-weight: 400;

                    @media (max-width: 600px) {
                        font-size: 2rem;
                    }
                }
                .title-line{
                    display: inline-block;
                    height: 1px;
                    width: 50%;
                    background: var(--text-color);
                    vertical-align: middle;
                }
            }
        }
    }

    .post-content{
        margin: auto;
        width: 100%;
        font-family: 'DM Sans';

        .tldr{
            background: #ff653433;
            border-radius: 10px;
            width: 100%;
            border-color: #f8b240;
            border-width:1px;
            border-style: solid;

            margin: auto;
            padding: 24px;
            box-sizing: border-box;
            margin-bottom: 2rem;

            @media (max-width: 600px) {
                padding: 16px;
            }
            h2{
                width: 100%;
                margin: 0px 0px 20px;
                color: #f8b240;
                font-size: 1rem;
                text-transform: uppercase;

                @media (max-width: 600px) {
                    font-size: 0.8rem;
                    line-height: 1.5rem;
                }
            }

            p{
                width: 100%;
                font-size: 1.1rem;
                line-height: 2rem;
                font-style: italic;

                @media (max-width: 600px) {
                    font-size: 0.9rem;
                    line-height: 1.5rem;
                }
            }
        }

        a:visited, a:link{
            color: var(--text-color);
        }
        h5{
            color: var(--secondary-highlight);
            letter-spacing: 2px;
            font-size: 1.5rem;
            margin: 1rem auto 10px;
            width: 95%;

            @media (max-width: 600px) {
                font-size: 1.2rem;
            }
        }
        h4{
            color: var(--secondary-highlight);
            font-size: 1.6rem;
            margin: 6px auto 6px;
            width: 95%;
            font-weight: 400;

            @media (max-width: 600px) {
                font-size: 1.4rem;
            }
        }
        p{
            width: 95%;
            margin: 0px auto 0px auto;
            font-size: 1.35rem;
            line-height: 2.5rem;

            @media (max-width: 600px) {
                font-size: 1.1rem;
                line-height: 1.8rem;
            }
        }
        img{
            max-width: 500px;
            width: 85%;
            display: block;
            margin: auto;
        }
        p.highlight{
            color: var(--secondary-highlight);
            padding-left: 20px;
            box-sizing: border-box;
            border-style: solid;
            border-width: 0px 0px 0px 1px;
            font-weight: 600;
        }
        div.box{
            width: 95%;
            padding: 10px 0px;
            margin: auto;
            background: #FF653433;
            border-radius: 10px;
            img{
                width: 90%;
            }
            p{
                width: 90%;
                font-size: 1.2rem;

                @media (max-width: 600px) {
                    font-size: 1rem;
                    line-height: 1.5rem;
                }
            }
            h5{
                text-align: center;
                font-family: 'DM Sans';
                font-weight: 400;
                font-size: 14px;
                letter-spacing: 3px;
            }
        }
        .separator{
            background: url('./separator.svg');
            height: 11px;
            width: 38px;
            margin: auto;
        }
    }
}